import "core-js/modules/es.array.push.js";
import { defineComponent, computed, ref } from 'vue';
import { useStore } from '@/store';
import { useRouter, useRoute } from 'vue-router';
import { pathMapToMenu } from '@/utils/map-menus'; // import { IRootState } from '@/store/types'

export default defineComponent({
  props: {
    collapse: {
      type: Boolean,
      default: false
    }
  },

  setup() {
    // 获取store
    const store = useStore();
    const userMenus = computed(() => store.state.login.userMenus); // 计算属性过滤icon字段并首字母大写

    const filterIcon = icon => {
      const iconStr = icon.replace('el-icon-', '');
      return iconStr.charAt(0).toUpperCase() + iconStr.slice(1);
    }; // 获取路由实例


    const router = useRouter(); // 获取当前路由

    const route = useRoute();
    const currentPath = route.path; // 根据当前路由获取菜单

    const menu = pathMapToMenu(userMenus.value, currentPath);
    const defaultValue = ref(menu.id + ''); // 菜单点击事件

    const handleMenuItemClick = item => {
      // console.log(item)
      // 跳转路由
      router.push({
        path: item.url ?? '/not-found'
      });
    };

    return {
      userMenus,
      defaultValue,
      filterIcon,
      handleMenuItemClick
    };
  }

});